import { Link } from 'react-router-dom'
import { Post } from '../../types'
import { formatDate } from '../../utils/date'
import clsx from 'clsx'

interface PostCardProps {
  post: Post
  className?: string
  variant?: 'default' | 'featured'
}

export function PostCard({ post, className, variant = 'default' }: PostCardProps) {
  const isFeatured = variant === 'featured'

  return (
    <Link 
      to={`/post/${post.slug}`}
      className={clsx(
        "group flex flex-col p-6 rounded-3xl border transition-all duration-300 relative overflow-hidden",
        isFeatured ? "bg-gradient-to-br from-indigo-500 to-purple-600 border-transparent text-white" : "bg-zinc-50 dark:bg-zinc-900 border-zinc-200 dark:border-zinc-800 hover:border-zinc-300 dark:hover:border-zinc-700",
        className
      )}
    >
      {/* Decorative background for featured */}
      {isFeatured && (
        <div className="absolute top-0 right-0 -mt-10 -mr-10 w-40 h-40 bg-white/10 rounded-full blur-2xl" />
      )}

      <div className="flex items-center justify-between mb-4 relative z-10">
        <div className="flex gap-2 flex-wrap">
          {post.category.map((cat) => (
            <span 
              key={cat} 
              className={clsx(
                "px-3 py-1 rounded-full text-xs font-medium",
                isFeatured ? "bg-white/20 text-white" : "bg-zinc-200 dark:bg-zinc-800 text-zinc-600 dark:text-zinc-400"
              )}
            >
              {cat}
            </span>
          ))}
        </div>
        <time className={clsx("text-sm shrink-0 ml-2", isFeatured ? "text-white/80" : "text-zinc-500 dark:text-zinc-400")}>
          {formatDate(post.date)}
        </time>
      </div>
      
      <h3 className={clsx(
        "font-bold mb-2 transition-colors relative z-10",
        isFeatured ? "text-3xl md:text-4xl leading-tight mb-4" : "text-xl group-hover:text-blue-600 dark:group-hover:text-blue-400"
      )}>
        {post.title}
      </h3>
      
      {post.description && (
        <p className={clsx(
          "line-clamp-3 mb-4 flex-grow relative z-10",
          isFeatured ? "text-white/90 text-lg" : "text-zinc-600 dark:text-zinc-400"
        )}>
          {post.description}
        </p>
      )}
      
      <div className={clsx(
        "flex items-center text-sm font-medium mt-auto relative z-10",
        isFeatured ? "text-white" : "text-blue-600 dark:text-blue-400"
      )}>
        Read more
        <svg className="w-4 h-4 ml-1 group-hover:translate-x-1 transition-transform" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 8l4 4m0 0l-4 4m4-4H3" />
        </svg>
      </div>
    </Link>
  )
}
