import { useAtom } from 'jotai'
import { postsAtom } from '../atoms/posts'
import { Link } from 'react-router-dom'
import { useMemo } from 'react'
import dayjs from 'dayjs'

export default function ArchivePage() {
  const [posts] = useAtom(postsAtom)

  // 
  const postsByYear = useMemo(() => {
    const sortedPosts = [...posts].sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())
    
    const grouped: Record<string, typeof posts> = {}
    
    sortedPosts.forEach(post => {
      const year = dayjs(post.date).format('YYYY')
      if (!grouped[year]) {
        grouped[year] = []
      }
      grouped[year].push(post)
    })
    
    return grouped
  }, [posts])

  const years = Object.keys(postsByYear).sort((a, b) => Number(b) - Number(a))

  return (
    <div className="max-w-4xl mx-auto py-10 px-4 sm:px-6 lg:px-8">
      {/* Timeline Container */}
      <div className="relative border-l-2 border-zinc-200 dark:border-zinc-800 ml-3 space-y-12">
        
        {/* Page Header Node */}
        <div className="relative pl-8 pt-1">
          {/* Dot */}
          <div className="absolute -left-[7px] top-2.5 w-3 h-3 rounded-full bg-zinc-400 dark:bg-zinc-600 ring-4 ring-white dark:ring-zinc-950"></div>
          <p className="text-zinc-600 dark:text-zinc-400 text-lg">
            Excellent! {posts.length} posts in total. Keep on writing.
          </p>
        </div>

        {/* Years List */}
        {years.map(year => (
          <div key={year} className="relative">
             {/* Year Node */}
             <div className="relative pl-8 mb-8">
                <div className="absolute -left-[7px] top-2.5 w-3 h-3 rounded-full bg-zinc-400 dark:bg-zinc-600 ring-4 ring-white dark:ring-zinc-950"></div>
                <h2 className="text-3xl font-bold text-zinc-800 dark:text-zinc-100">{year}</h2>
             </div>
             
             {/* Posts List */}
             <div className="space-y-6"> 
                {postsByYear[year].map(post => (
                   <div key={post.id} className="relative pl-8 flex items-center group">
                      {/* Post Dot */}
                      <div className="absolute -left-[5px] top-2.5 w-2 h-2 rounded-full bg-zinc-300 dark:bg-zinc-700 group-hover:bg-zinc-500 transition-colors"></div>
                      
                      {/* Date */}
                      <span className="text-zinc-500 dark:text-zinc-500 mr-4 font-mono text-sm whitespace-nowrap">
                        {dayjs(post.date).format('MM-DD')}
                      </span>
                      
                      {/* Title */}
                      <Link 
                        to={`/post/${post.slug}`} 
                        className="text-zinc-700 dark:text-zinc-300 hover:text-black dark:hover:text-white font-medium mr-4 whitespace-nowrap transition-colors"
                      >
                        {post.title}
                      </Link>
                      
                      {/* Dashed Line */}
                      <div className="flex-grow border-b border-dashed border-zinc-200 dark:border-zinc-800 relative top-1"></div>
                   </div>
                ))}
             </div>
          </div>
        ))}
      </div>
    </div>
  )
}
