import { atom } from 'jotai'
import { Post, Category } from '../types'
import { loadPosts, getCategories } from '../utils/posts'

export const postsAtom = atom<Post[]>([])
export const categoriesAtom = atom<Category[]>([])
export const selectedCategoryAtom = atom<string>('')
export const loadingAtom = atom<boolean>(true)

export const filteredPostsAtom = atom((get) => {
  const posts = get(postsAtom)
  const selectedCategory = get(selectedCategoryAtom)
  
  if (!selectedCategory) return posts
  
  return posts.filter(post => 
    post.category.some(c => c.toLowerCase() === selectedCategory.toLowerCase())
  )
})

export const initPostsAtom = atom(
  null,
  (get, set) => {
    const posts = loadPosts()
    const categories = getCategories(posts)
    
    set(postsAtom, posts)
    set(categoriesAtom, categories)
    set(loadingAtom, false)
  }
)
