import { Post } from '../../types'
import Markdown from 'react-markdown'
import remarkGfm from 'remark-gfm'
import rehypeHighlight from 'rehype-highlight'
import { formatDate } from '../../utils/date'
import 'highlight.js/styles/github-dark.css'
import { Link } from 'react-router-dom'
import { ArrowLeft } from 'lucide-react'

interface PostDetailProps {
  post: Post
}

export function PostDetail({ post }: PostDetailProps) {
  return (
    <article className="max-w-3xl mx-auto py-8">
      <Link to="/" className="inline-flex items-center text-sm text-zinc-500 hover:text-zinc-900 dark:hover:text-zinc-100 mb-8 transition-colors">
        <ArrowLeft size={16} className="mr-2" />
        Back to Home
      </Link>
      
      <header className="mb-8">
        <div className="flex items-center gap-4 mb-4 flex-wrap">
          {post.category.map((cat) => (
            <span key={cat} className="px-3 py-1 rounded-full text-xs font-medium bg-zinc-100 dark:bg-zinc-800 text-zinc-600 dark:text-zinc-400">
              {cat}
            </span>
          ))}
          <time className="text-sm text-zinc-500 ml-auto">
            {formatDate(post.date)}
          </time>
        </div>
        <h1 className="text-3xl md:text-4xl font-bold mb-4 text-zinc-900 dark:text-zinc-50">
          {post.title}
        </h1>
        {post.description && (
          <p className="text-xl text-zinc-600 dark:text-zinc-400">
            {post.description}
          </p>
        )}
      </header>
      
      <div className="prose prose-zinc dark:prose-invert max-w-none">
        <Markdown 
          remarkPlugins={[remarkGfm]} 
          rehypePlugins={[rehypeHighlight]}
        >
          {post.content}
        </Markdown>
      </div>
    </article>
  )
}
