import { useEffect } from 'react'
import { Outlet, useLocation } from 'react-router-dom'
import { Header } from './Header'
import { Footer } from './Footer'
import { useSetAtom } from 'jotai'
import { initThemeAtom } from '../../atoms/theme'
import { initPostsAtom } from '../../atoms/posts'

export function Layout() {
  const initTheme = useSetAtom(initThemeAtom)
  const initPosts = useSetAtom(initPostsAtom)
  const { pathname } = useLocation()

  useEffect(() => {
    initTheme()
    initPosts()
  }, [initTheme, initPosts])

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [pathname])

  return (
    <div className="min-h-screen bg-white dark:bg-zinc-950 text-zinc-900 dark:text-zinc-50 transition-colors duration-300">
      <Header />
      <main className="container mx-auto px-4 min-h-[calc(100vh-160px)]">
        <Outlet />
      </main>
      <Footer />
    </div>
  )
}
