import { useAtom } from 'jotai'
import { filteredPostsAtom, loadingAtom } from '../../atoms/posts'
import { PostCard } from './PostCard'
import clsx from 'clsx'

export function PostList() {
  const [posts] = useAtom(filteredPostsAtom)
  const [loading] = useAtom(loadingAtom)

  if (loading) {
    return <div className="py-12 text-center text-zinc-500">Loading posts...</div>
  }

  if (posts.length === 0) {
    return <div className="py-12 text-center text-zinc-500">No posts found.</div>
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 auto-rows-[minmax(200px,auto)]">
      {posts.map((post, index) => (
        <PostCard 
          key={post.id} 
          post={post} 
          variant={index === 0 ? 'featured' : 'default'}
          className={clsx(
            index === 0 && "md:col-span-2 md:row-span-2 min-h-[400px]", // Hero post
            // No other special sizing to keep it balanced
          )}
        />
      ))}
    </div>
  )
}
