import { Post, Category, PostFrontmatter } from '../types'

// Using Vite's import.meta.glob to get the parsed module from our custom plugin
const postModules = import.meta.glob('/src/posts/*.md', { 
  eager: true
})

export function loadPosts(): Post[] {
  return Object.entries(postModules).map(([path, module]) => {
    const filename = path.split('/').pop() || ''
    const slug = filename.replace('.md', '')
    
    // Module is pre-parsed by vite plugin
    const { attributes, content } = module as { attributes: PostFrontmatter, content: string }
    const frontmatter = attributes
    
    // Normalize category to string array
    const rawCategory = frontmatter.category || 'Uncategorized'
    const categories = Array.isArray(rawCategory) ? rawCategory : [rawCategory]
    
    return {
      ...frontmatter,
      content,
      slug,
      id: frontmatter.id || slug,
      date: frontmatter.date ? new Date(frontmatter.date).toISOString() : new Date().toISOString(),
      category: categories
    }
  }).sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())
}

export function getPostsByCategory(posts: Post[], category: string): Post[] {
  return posts.filter(post => 
    post.category.some(c => c.toLowerCase() === category.toLowerCase())
  )
}

export function getCategories(posts: Post[]): Category[] {
  const categoryMap = new Map<string, Post[]>()
  
  posts.forEach(post => {
    post.category.forEach(cat => {
      if (!categoryMap.has(cat)) {
        categoryMap.set(cat, [])
      }
      categoryMap.get(cat)!.push(post)
    })
  })
  
  return Array.from(categoryMap.entries()).map(([name, posts]) => ({
    name,
    count: posts.length,
    posts
  }))
}
